.Register {
  background-image: url("../../Assets/imgLogin/BACKGROUND.png");
  width: 100%;
  height: 100vh;
  background-attachment: fixed;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  overflow: hidden;
  display: flex;
  align-items: center;
}

.Filter {
  width: 100%;
  height: 100vh;
  background-color: #0000002c;
  position: absolute;
}

.Form {
  background: #e6eff76a;
  backdrop-filter: blur(0.4em);
  -webkit-backdrop-filter: blur(0.4em);
  width: fit-content;
  margin: auto;
  border-radius: 10px;
  padding: 10px 10px 10px 15px;
  /* border: 1px solid #fff; */
  box-shadow: 0px 0px 7px #000;
  display: flex;
  justify-content: center;
  align-items: center;
}

.Title {
  font-size: 3em;
  padding-bottom: 0.4em;
}

.Error {
  color: red;
  font-size: 15px;
  margin-top: 7px;
  margin-bottom: 7px;
  text-align: left;
}

.Form button {
  background-color: #1d92ff;
  border: none;
  border-radius: 5px;
  color: #fff;
  min-width: 100%;
  cursor: pointer;
  margin-top: 20px;
  padding: 10px;
  transition: 0.5s;
  letter-spacing: 2px;
  font-size: 1em;
  font-weight: lighter;
  font-family: sans-serif;
}

.Form button:hover {
  background-color: hwb(209 44% 0%);
}

.Form button:disabled {
  background-color: #84c3fd;
  color: #ffffff93;
}

.InputAndLabel {
  max-width: 100%;
}

.InputAndLabel label {
  pointer-events: none;
  display: block;
}

.InputAndLabel input {
  background: none;
  outline: none;
  border: none;
  border-bottom: 1px solid #000000;
  left: 5px;
  display: block;
  width: 100%;
  text-decoration: none;
  padding: 10px 0px 5px 5px;
  font-size: 1em;
}

.Password {
  position: relative;
  margin-top: 1em;
}

.Email {
  position: relative;
}

.ConfirmPassword {
      position: relative;
}

.toRegister {
    margin-top: 1.5em;
}

.NavLink {
    color: #0041a9;
}

.Slide1 {
    padding-left: 10px;
    max-width: 30vw;
    opacity: 1;
    border-radius: 10px;
    margin-left: 5px;
}